.list {
  flex: 1 0 65%;
  padding-left: 10%;
}

.list__item {
  font-weight: bold;
  font-size: 18px;
  line-height: 37px;
  margin-top: 15px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    height: 2px;
    width: 10px;
    background-color: $forest-green;
    left: -30px;
    top: 50%;
    transform: translateY(-50%);
  }
}