.try-now {
  position: relative;
  padding: 0 15px 140px;

  .intro__img {
    &::before {
      background: $light-green;
    }
  }
}

.intro__text--try-now {
  color: #000;
  font-weight: 300;
  font-size: 24px!important;
  top: 290px;

  strong {
    font-weight: bold;
  }
}

.try-now__summary,
.try-now-subscribe__info {
  max-width: 680px;
  font-size: 18px;
  line-height: 2;
  letter-spacing: 0.4px;
}

.try-now__summary {
  margin: 80px 0 0 6vw;
}

.try-now-subscribe {
  margin-top: 80px;
  display: flex;
  justify-content: space-between;

  .contacts-form {
    flex: 1 0 40%;
    margin-top: 0;
  }
}

.try-now-subscribe__info {
  flex: 1 0 60%;
}

.try-now-subscribe__paragraph {
  max-width: 450px;
}

.try-now-subscribe__paragraph:last-child {
  margin-top: 30px;
}

.try-now__img--big {
  position: absolute;
  top: 110px;
  right: 0;
  animation: move 4s ease-out infinite;
}

.try-now__img--small {
  position: absolute;
  top: 0;
  right: 0;
}

@keyframes move {
  0% {
    transform: translateY(25px);
  }

  50% {
    transform: translateY(-30px);
  }

  100% {
    transform: translateY(25px);
  }
}