.about {
  .container {
    margin-left: 27%;
    max-width: 970px;
  }

  .intro.container {
    max-width: 1200px;
    margin: 0 auto;
  }
}

.structure {
  margin-top: 95px;
  background-color: $light-green;
  padding: 75px 15px 90px;
  position: relative;

  .container {
    max-width: 760px;
    margin: 0 auto;
  }

  .text {
    margin-top: 50px;
    font-size: 18px;
  }

  .industries-list {
    margin-top: 60px;
  }
}

.hierarchy {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 60px;
}

.hierarchy__item {
  flex: 1 0 170px;
  text-align: center;
  border: 1px solid #D1DBE3;
  border-radius: 5px;
  height: 170px;
  margin-top: 30px;
  align-content: center;
  max-width: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  &:first-child {
    max-width: none;
    height: 200px;
    flex: 1 0 100%;
  }
}

.hierarchy__item-position {
  font-size: 14px;
  font-weight: bold;
  line-height: 25px;
}

.hierarchy__item-mail {
  font-size: 14px;
}

.navigation--about {
  position: fixed;
  left: -15%;
  font-size: 14px;
  z-index: 100;
  height: 181px;
  justify-content: space-between;
  top: 57%;
  flex-direction: column;

  .navigation__link {
    position: relative;
    font-size: 14px;

    &:hover {
      color: #000;
    }
  }
}

.navigation__logo {
  position: absolute;
  bottom: 0;
  left: -25px;
}

.add-img::before {
  color: #000;

  content: url('../images/nav-logo.svg');
  position: absolute;
  left: -40px;
  bottom: 0;
}