.values {
  background-color: $light-green;
  padding: 75px 15px 150px;
  position: relative;
}

.values-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 115px;
}

.values__item {
  flex: 0 1 176px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.values__item-heading {
  color: $forest-green;
  text-transform: uppercase;
  line-height: 16px;
  font-weight: bold;
  letter-spacing: 2px;
  margin-bottom: 33px;
}

.values-slider {
  max-width: 870px;
  margin: 65px 0 0;

  .arrow-link {
    bottom: -90px;
  }

  .arrow-link--next {
    right: 44%;
  }

  .arrow-link--prev {
    left: 44%;
  }
}
