.tabs{
  display: flex;
  justify-content: space-between;
  margin-top: 90px;
}

.tab-link {
  color: #BDBDBD;
  font-weight: bold;
  font-size: 14px;
  letter-spacing: 0.4px;
  line-height: 25px;
  flex: 1 0 13%;
  cursor: pointer;
  border: 1px solid #D1DBE3;
  border-radius: 3px;
  margin-right: 20px;
  display: flex;
  align-items: center;
  text-align: center;
  height: 70px;
  justify-content: center;
  background-color: #ffffff;
  transition: all 0.25s;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    color: $forest-green;
  }

  &.is-active {
    border: transparent;
    transform: translateY(-15px);
    color: $forest-green;
    box-shadow: 3px 5px 20px rgba(0, 0, 0, 0.1);
  }
}

.tab-content{
  display: none;
  background: #fff;
  margin-top: 90px;
  padding: 15px 15px 140px;
}

.tab-content.is-active{
  display: flex;
}

.industries-list {
  flex: 1 0 65%;
  padding-left: 10%;
}

.industries-list__item {
  font-weight: bold;
  font-size: 18px;
  line-height: 37px;
  margin-top: 15px;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    height: 2px;
    width: 10px;
    background-color: $forest-green;
    left: -30px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.industries-logo-wrapper {
  flex: 1 0 28%;
}

.industries-logo__img {
  display: block;
  margin-top: 30px;

  &:first-child {
    margin-top: 0;
  }
}