.vacancies {
  padding: 75px 15px 150px;
  position: relative;

  .light-text {
    text-align: center;
    margin-top: 130px;
  }
}

.vacancies-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
}

.vacancies__item {
  flex: 0 1 170px;
  border-radius: 5px;
  border: 1px #D1DBE3 solid;
  height: 170px;
  padding-top: 20px;
  text-align: center;
  transition: transform 0.3s;
  z-index: 100;

  &:hover {
    border: 0;
    box-shadow: 2px 2px 30px 0 rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transform: translateY(-20px);

    &.is-open {
      box-shadow: none;
      transform: translateY(0);
      border: none;
    }
  }
}

.vacancies__item-position {
  color: $forest-green;
  font-weight: bold;
  line-height: 2;
  font-size: 18px;
}

.vacancy-info {
  position: relative;
  width: 80vw;
  top: 0;
  transform: translateY(-60%);
  box-shadow: 2px 2px 50px 10px rgba(0, 0, 0, 0.1);
  padding: 70px 70px 30px;
  background-color: #F6FCF9;
  display: none;
  right: 21%;

  .list {
    text-align: left;
    padding-left: 50px;
    max-width: 720px;
    margin-top: 50px;
  }

  .list__item {
    font-weight: 300;
    color: $grey;
    display: none;
  }

  .list__item::before {
    left: -50px;
  }
}

.vacancy-info-header {
  display: flex;
}

.vacancy-info-header__item {
  color: $forest-green;
  font-size: 14px;
  letter-spacing: 0.5px;

  &:last-child {
    margin-left: 68px;

    img {
      margin-left: 5px;
    }
  }
}

.vacancy-info__position,
.vacancy-info__caption {
  font-size: 48px;
  font-weight: bold;
  line-height: 61px;
  max-width: 540px;
  letter-spacing: 1px;
  text-align: left;
  margin-top: 30px;
}

.vacancy-info__caption {
  font-weight: 200;
  color: $grey;
  margin-top: 65px;
  max-width: none;
}

.vacancy__btn {
  margin: 100px auto 0;
  width: 230px;
}

.vacancy__application {
  margin: 80px auto;
  background-color: #fff;
  box-shadow: 2px 2px 30px 0 rgba(0, 0, 0, 0.1);
  padding: 60px 3%;
  max-width: 570px;
}

.vacancy-form__details {
  display: flex;
  justify-content: space-between;
}

.vacancy-form__label {
  color: $grey;
  font-size: 14px;
  width: 47%;
  text-align: left;
  margin-top: 20px;
  display: block;
}

.vacancy-form__input {
  border: 1px solid #D1DBE3;
  background-color: #fff;
  height: 40px;
  padding: 10px;
  width: 100%;
  margin-top: 10px;
  border-radius: 5px;
}

.vacancy-form__mssg {
  height: 120px;
}

.resume__input {
  display: none;
}

.resume {
  margin: 40px auto 0;
  text-align: center;
}

.resume__label {
  color: $forest-green;
  position: relative;
  font-size: 18px;
  cursor: pointer;

  &::before {
    content: '.txt, .doc, .pdf';
    position: absolute;
    color: $grey;
    font-size: 14px;
    width: 100%;
    top: 30px;
    left: 0;
  }
}

.vacancy-form__upload {
  margin: 60px auto 0;
  width: 170px;
}

.vacancy__arrow {
  margin-left: 40px;
  margin-bottom: 7px;
  width: 23px;
}