.partnership {
  padding: 76px 15px 60px;
  position: relative;
}

.partnership-instruments-wrapper {
  display: flex;
  justify-content: space-between;
  margin-top: 55px;
}

.partnership-instruments__item {
  flex: 0 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  max-width: 165px;
}

.partnership-instruments__text {
  color: $grey;
  font-size: 14px;
  margin-top: 50px;
  line-height: 25px;
}

.partnership__proof {
  position: absolute;
  top: 40%;
  right: 10%;
}

.partnership__title {
  color: $forest-green;
  font-weight: bold;
  text-align: center;
  margin-top: 101px;
}

.partnership__caption {
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  font-size: 14px;
  margin-top: 70px;
  letter-spacing: 2px;
}